import React from 'react';
import { FilterStyle } from '../assets/styles/FilterStyle.jsx';
import { FilterHead } from '../assets/styles/FilterHead.jsx';
import { FilterElement } from '../assets/styles/FilterElement.jsx';
import { CheckBoxStyle } from '../assets/styles/CheckBoxStyle.jsx';
import checked from '../assets/media/checked.png';
import unchecked from '../assets/media/unchecked.png';

export default class Filter extends React.PureComponent {
  render() {
    const {
      all, no, one, two, three,
    } = this.props.filter;
    const { handleCheckFilter } = this.props;
    return (
      <FilterStyle>
        <FilterHead>
          КОЛИЧЕСТВО ПЕРЕСАДОК
        </FilterHead>
        <FilterElement onClick={handleCheckFilter('all')}>
          <CheckBoxStyle src={all ? checked : unchecked} alt="" />
          Все
        </FilterElement>
        <FilterElement onClick={handleCheckFilter('no')}>
          <CheckBoxStyle src={no ? checked : unchecked} alt="" />
          Без пересадок
        </FilterElement>
        <FilterElement onClick={handleCheckFilter('one')}>
          <CheckBoxStyle src={one ? checked : unchecked} alt="" />
          1 пересадка
        </FilterElement>
        <FilterElement onClick={handleCheckFilter('two')}>
          <CheckBoxStyle src={two ? checked : unchecked} alt="" />
          2 пересадки
        </FilterElement>
        <FilterElement onClick={handleCheckFilter('three')}>
          <CheckBoxStyle src={three ? checked : unchecked} alt="" />
          3 пересадки
        </FilterElement>
      </FilterStyle>
    );
  }
}
