/* eslint-disable react/prefer-stateless-function */
import React from 'react';
import Filter from '../components/Filter.jsx';
import Tickets from '../components/Tickets.jsx';
import '../assets/styles/App.css';
import logo from '../assets/media/Logo.png';
import { MainStyle } from '../assets/styles/MainStyle.jsx';
import { CommonStyle } from '../assets/styles/CommonStyle.jsx';

export default class Aviasales extends React.Component {
  state = {
    filter: {
      all: false,
      no: false,
      one: false,
      two: false,
      three: false,
    },
    tickets: 0,
    tabs: 1,
  }

  handleCheckFilter = (name) => (e) => {
    e.preventDefault();
    const {
      all, no, one, two, three,
    } = this.state.filter;
    switch (name) {
      case 'all':
        this.setState({
          filter: {
            all: !all,
            no,
            one,
            two,
            three,
          },
        });
        break;
      case 'no':
        this.setState({
          filter: {
            all,
            no: !no,
            one,
            two,
            three,
          },
        });
        break;
      case 'one':
        this.setState({
          filter: {
            all,
            no,
            one: !one,
            two,
            three,
          },
        });
        break;
      case 'two':
        this.setState({
          filter: {
            all,
            no,
            one,
            two: !two,
            three,
          },
        });
        break;
      case 'three':
        this.setState({
          filter: {
            all,
            no,
            one,
            two,
            three: !three,
          },
        });
        break;
      default:
        alert('Ошибка');
    }
  }

  handleTabChanger = (tab) => (e) => {
    e.preventDefault();
    switch (tab) {
      case '1':
        this.setState({ tabs: 1 });
        break;
      case '2':
        this.setState({ tabs: 2 });
        break;
      default:
        alert('Ошибка');
    }
  }

  render() {
    const { filter, tabs } = this.state;
    return (
      <CommonStyle>
        <img src={logo} alt="logo" />
        <MainStyle>
          <Filter filter={filter} handleCheckFilter={this.handleCheckFilter} />
          <Tickets tabs={tabs} handleTabChanger={this.handleTabChanger} />
        </MainStyle>
      </CommonStyle>
    );
  }
}
