import React from 'react';
import { LeftButton, RightButton } from '../assets/styles/TabButton.jsx';

export default class Tabs extends React.Component {
  render() {
    const { tabs, handleTabChanger } = this.props;
    return (
      <div>
        <LeftButton
          tabs={tabs}
          onClick={handleTabChanger('1')}
          left="true"
        >
          САМЫЙ ДЕШЕВЫЙ
        </LeftButton>
        <RightButton
          tabs={tabs}
          onClick={handleTabChanger('2')}
          right="true"
        >
          САМЫЙ БЫСТРЫЙ
        </RightButton>
      </div>
    );
  }
}
